.App {
  height: 100dvh;
  overflow: hidden;
  background: center / cover no-repeat url("./images/background.png");

  &.white {
    background: #fff;
    height: 100vh;

    @media screen and (max-width: 1025px) {
      height: 100vh;
    }

    // @media screen and (min-width: 1025px) and (min-height: 400px) and (max-height: 799px) {
    //   overflow: scroll;
    // }
  }
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px;

  @media screen and (max-width: 1025px) {
    padding: 0 20px;
  }
}

.btn-purple {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 460px;
  height: 130px;
  padding: 0;
  border: none;
  appearance: none;
  color: #fff;
  font-family: "Grtsk Giga";
  outline: none;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  background-color: transparent;
  pointer-events: all;
  position: relative;

  br {
    @media screen and (min-width: 1025px) {
      display: none;
    }
  }

  @media screen and (max-width: 1025px) {
    font-size: 16px;
  }

  @media screen and (max-width: 769px) {
    width: 300px;
  }

  @media screen and (min-width: 1025px) {
    &:hover {
      &::after {
        transform: translate(-50%, -50%) scale(1, 1.4);
      }
    }
  }

  &:active {
    &::before {
      opacity: 1;
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 54%;
    width: 441px;
    height: 200px;
    background: center / 100% no-repeat url("/images/purple_active.svg");
    z-index: 1;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: opacity 0.5s ease;

    @media screen and (max-width: 769px) {
      width: 350px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 466px;
    height: 137px;
    z-index: -1;
    transform: translate(-50%, -50%);
    background: center / 100% no-repeat url("/images/star_purple.png");
    transition: transform 0.5s ease;

    @media screen and (max-width: 769px) {
      width: 390px;
    }

    @media screen and (max-width: 375px) {
      width: 300px;
    }
  }
}

