.btn-back {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 40px;
  position: relative;
  width: 190px;
  height: 100px;
  font-size: 18px;
  letter-spacing: -0.03em;
  font-weight: 700;
  color: #6018f2;
  z-index: 1;
  cursor: pointer;

  @media screen and (max-width: 1025px) {
    width: 175px;
    height: 55px;
    font-size: 16px;
  }

  @media screen and (min-width: 1025px) {
    &:hover {
      &::after {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &:active {
    &::after {
      transform: translateY(-15px);
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 2px;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: center / contain no-repeat url("/images/back.svg");

    @media screen and (max-width: 1025px) {
      background: center / contain no-repeat url("/images/back_mob.svg");
    }
  }

  @media screen and (min-width: 1025px) {
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      top: 10px;
      left: 5px;
      height: 100%;
      background: center / contain no-repeat url("/images/back_after.svg");
      z-index: -2;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.5s ease;
    }
  }
}
